.AboutMe{
    clear: both;
    width: 100%;
    background: #F0EEE2;
    height: 500px;
    .contents{
        width: 100%;
        margin-top: 100px;
        display: flex;
        justify-content: space-around;
        .item-box{
            background: #fff;
            overflow: hidden;
            width: calc((100% - 120px)/5);
            max-width: 180px;
            border-radius: 50%;
            padding: 20px;
            display: flex;
            justify-content: center;
            align-items: center;
            .item{
                position: relative;
                top: 0;
                width: 100%;
                height: 100%;
                text-align: center;
                .item-image{
                    img{
                        width: 50%;
                    }
                }
                .item-text{
                    font-size: 16px;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                }
                .item-text.position{font-size: 14px;}
                .item-text.email{font-size: 12px;}
                    
            }
        }
    }
}


/* 태블릿 */
@media screen and (max-width: 1023px) {
    .AboutMe{
        .contents{
            margin-top: 40px;
            flex-wrap: wrap;
            align-content: stretch;
            .item-box{
                margin: 20px;
                .item{
                    font-size: 14px;
                    .item-image{
                        img{
                            width: 50%;
                        }
                    }
                    .item-text.position{font-size: 12px;}
                    .item-text.email{font-size: 11px;}
                }
            }
        }
    }
}

/* 모바일 */
@media screen and (max-width: 767px) {
    .AboutMe{
        .contents{
            .item-box{
                width: calc(100%/4 - 20px);
                .item{
                    .item-text{
                        font-size: 14px;
                    }
                }
            }
        }
    }
}