.Footer{
    clear: both;
    width: 100%;
    height: 140px;
    background: #39424C;
    .left{
        width: calc(50% - 60px);
        float: left;
        padding: 20px;
    }
    .right{
        width: calc(50% - 60px);
        float: right;
        padding: 20px;
    }
    p{
        font-size: 14px;
        margin: 8px 0;
        color: #eee;
    }
    a{
        text-decoration: none;        
        color: #fff;
    }
    a:visited{
        color: #fff;
    }
}

/* 태블릿 */
@media screen and (max-width: 1023px) {
    .Footer{
        height: 82px;
        .left{
            width: calc(50% - 20px);
            padding: 10px;
        }
        .right{
            width: calc(50% - 20px);
            padding: 10px;
        }
        p{
            font-size: 10px;
            margin: 4px 0;
        }
    }
}

/* 모바일 */
@media screen and (max-width: 767px) {
    .Footer{
        height: 82px;
        .left{
            width: calc(50% - 20px);
            padding: 10px;
        }
        .right{
            width: calc(50% - 20px);
            padding: 10px;
        }
        p{
            font-size: 4px;
            margin: 4px 0;
        }
    }
}