.Record{
    clear: both;
    width: 100%;
    height: 700px;
    background: #fff;
    .contents{
        margin: 20px 0;
        padding: 0 10%;
    }
    .record{
        margin-top: 50px;
        .record_title{
            width: 100%;
            font-size: 28px;
            font-weight: 700;
            text-align: center;
            margin: 25px;
        }        
        .record_area{
            .contents{
                display: flex;
                flex-direction: column;
                align-items: center;
            }
        }
        .record_div_area{
            display: flex;
            justify-content: space-around;
            align-items: center;
            color: #4C4C4C;
            .contents{
                display: flex;
                justify-content: center;
                img{
                    width: 200px;
                }                
                .content_left{
                    font-size: 28px;
                    margin: auto 40px;
                }
                .content_right{
                    text-align: center;
                }
            }
        }
    }
    a{
        color: #4C4C4C;
    }
    a:hover{
        color:mediumblue;
    }
}

/* 태블릿 */
@media screen and (max-width: 1023px) {
    .Record{
        height: 800px;
        .contents{
            margin: 10px 0;
            padding: 0 20px;
        }
        .record{
            .record_title{
                font-size: 20px;
            }
            .record_div_area{
                flex-direction: column;
                .contents{
                    margin: 40px 0;
                    img{
                        width: 100px;
                    }                
                    .content_left{
                        font-size: 20px;
                        margin: auto 20px;
                    }
                    .content_right{
                        font-size: 20px;
                    }
                }
            }
        }
    }
}

/* 모바일 */
@media screen and (max-width: 767px) {
    .Record{
        height: 900px;
        .contents{
            margin: 7px 0;
            padding: 0 40px;
        }
        .record{
            .record_title{
                font-size: 16px;
            }
            .record_div_area{
                flex-direction: column;
                .contents{
                    margin: 30px 0;
                    img{
                        width: 80px;
                    }                
                    .content_left{
                        font-size: 16px;
                        margin: auto 5px;
                    }
                    .content_right{
                        font-size: 12px;
                        display: flex;
                        flex-direction: column;
                    }
                }
            }
        }
    }
}