.Careers{
    clear: both;
    width: 100%;
    height: 500px;
    background: #fff;
    // background: linear-gradient(#A4FFFF, #D4F4FA, #003399);
    .miniTitle{
        color: #1266FF;
    }
    .career{
        clear: both;
        margin-top: 50px;
        display: flex;
        justify-content: space-evenly;
        .career_area{
            width: 30%;
            .group_name{
                font-size: 18px;
                height: 48px;
            }
            ul{
                li{
                    font-size: 16px;
                    line-height: 16px;
                    ul{
                        li{
                            padding-left: 20px;
                        }
                        
                    }
                }
                
            }
        }
    }
}

ul, li{list-style: none;height: 32px;}

/* 태블릿 */
@media screen and (max-width: 1023px) {
    .Careers{
        .career{
            .career_area{
                .group_name{
                    font-size: 14px;
                    height: 36px;
                }
                ul{
                    li{
                        font-size: 12px;
                        line-height: 12px;
                    }
                }
            }
        }
    }    
}

/* 모바일 */
@media screen and (max-width: 767px) {
    .Careers{
        .career{
            .career_area{
                .group_name{
                    font-size: 12px;
                    height: 24px;
                }
                ul{
                    li{
                        font-size: 10px;
                        line-height: 10px;
                    }
                }
            }
        }
    }
}